import React, { Component } from 'react';
import Counter from './counter';

class LstCounters extends Component {
    render() {
        return (<div>
            <button className="btn btn-primary btn-sm m-3"
onClick={this.props.onClearCounters}>Clear Counters</button>
 { this.props.counters.map(counter => (<Counter key={counter.id} counter={counter} onDelete={this.props.onDelete} onIncrement={this.props.onIncrement} />)) }
        </div>)
    }
}

export default LstCounters;