import React, { Component } from 'react';

class Counter extends Component {
    render() {
        return (
        <div>
            <h5>{this.setLectureName()}</h5>
            <button className="btn btn-info m-3" type="button">Enrolled: 
                <span className={this.setBadgeClasses()}>{this.formatCt()}</span>
            </button>
            <button onClick={() => this.props.onIncrement(this.props.counter)} className="btn btn-primary btn-sm">Enroll</button>
            <button onClick={() => this.props.onDelete(this.props.counter.id)} className="btn btn-warning btn-sm m-3">Cancel</button>
        </div>
        );
    };

    setButtonClass() {
        let buttonclass = "btn btn-info";
        return buttonclass;
    }

    setLectureName() {
        const { name } = this.props.counter;
        return name;
    }

    setBadgeClasses() {
        let badgeclasses = "badge m-3";
        badgeclasses += (this.props.counter.value === 0) ? "badge-danger" : "badge-info";
        return badgeclasses;
    };

    formatCt() {
        const { value } = this.props.counter;
        return value === 0 ? "Zero" : value;
    };
}

export default Counter;